var CommonUtil = {

	
	// __proto__
	//__proto__: theProtoObj,

	getFnName(fn){
	    if(typeof fn !== "function") return "not a function";
	    var sName = (fn.name) ? fn.name : fn.toString().match(/function\s+([^(\(|\s)]+)/)[1];
	    return sName;
	},

	// animation by rAF
	// super.runAnimation(nWidthForAnimation, this.option.nDuration, {
	// 			'before' : fnBefore,
	// 			'after' : this.fnAfter
	// });
	runAnimation(nWidthForAnimation, nDuration, htFn) {
		if(htFn && htFn.before && (typeof htFn.before === "function")) { 
		 	htFn['before'].call();
		}

		this.bAnimationing = true;

		let sTF = this.getCSSName("transform");

		let elTarget = this.elTarget;
		let nStartTime = Date.now();

		let nPreviousTranslateX = this.getTranslate3dX(elTarget);


		function execAnimation() {

			let nNowTime = Date.now();
			let nDiffTime = nNowTime-nStartTime;

			if(nDiffTime > nDuration) {

				let nStep = nPreviousTranslateX + (nWidthForAnimation);
				elTarget.style[sTF] = 'translate3d(' + (nStep) + 'px, 0, 0)';
				this.bAnimationing =false;
				if(htFn && htFn.after && (typeof htFn.after === "function")) { 
					htFn['after'].call();
				}
				return;

			} else { 

				let nStep = nPreviousTranslateX + (nDiffTime / nDuration * nWidthForAnimation);
				elTarget.style[sTF] = 'translate3d(' + (nStep) + 'px, 0, 0)';
				window.requestAnimationFrame(execAnimation.bind(this));   

			}
		}

		window.requestAnimationFrame(execAnimation.bind(this));
	},

	setTranslate3dX(ele, nValue) {

		let sTF = this.getCSSName('transform');
		this.elTarget.style[sTF] = 'translate3d(' + (nValue) + 'px, 0, 0)';

	},

	getWidth(ele) {
		let nWidth = 0;
		if (ele.getBoundingClientRect().width) {
 			nWidth = ele.getBoundingClientRect().width;
		} else {
		  	nWidth = ele.offsetWidth;
		}

		return nWidth
	},

	getCSSName(sName) {
		if(this.htCacheData[sName]) return this.htCacheData[sName];

		var _htNameSet = {
						'transition' : ['webkitTransition', 'transition' ], 
						'transform' : ['webkitTransform', 'transform' ], 
				  }

		let aNameList = _htNameSet[sName];

		if(!this.isExist(aNameList)) return null;

		for(let i=0, len=aNameList.length; i < len ; i++) {
			if(typeof document.body.style[aNameList[i]] === 'string') this.htCacheData[sName] = aNameList[i];
			return this.htCacheData[sName];
		}
	},

	getTranslate3dX(ele) {
		let sTF = this.getCSSName("transform");
		let sPreCss = ele.style[sTF];
		let nPreX = +sPreCss.replace(/translate3d\((-*\d+(?:\.\d+)*)(px)*\,.+\)/g , "$1");
		return nPreX;
	},

	getCSSTransitionEnd() {
		let sTS = this.getCSSName('transition');
		let sTSE = (sTS === "webkitTransition") ? "webkitTransitionEnd" : "transitionend";
		return sTSE;
	},

	setCSS(el,style,value) {
		el.style[style] = value;
	},

	//check null or undefined
	isExist(data){
		return data != null;
	}
}
class CommonComponent {
	constructor(htOption) {
		this.htOption = htOption;
		this.htCacheData = {};
	}

	execOption (htValue, htDefaultValue, htStorage) {
		Object.keys(htDefaultValue).forEach((v,i,o) => {
			if(typeof htValue[v] === "undefined") {
				htStorage[v] = htDefaultValue[v];
			} else {
				htStorage[v] = htValue[v];
			}
		});
	}

	_addOnPlugin(fnPlugin, htPluginInstance, aPluginList, elTarget) {
		let sFunctionName = CommonUtil.getFnName(fnPlugin);
		if(aPluginList.indexOf(sFunctionName) < 0) return "unknown plugin";
		htPluginInstance[sFunctionName] = new fnPlugin(elTarget);
		return htPluginInstance[sFunctionName];
	}
}


class RecentWordPlugin extends CommonComponent {

	constructor(elTarget, htOption) {
		super(htOption);
		this.elTarget = elTarget;
		this.init(htOption);
	}

	init(htOption) {
		this._setDefaultOption();
		this.option = {};
		super.execOption(htOption, this._htDefaultOption, this.option);
		this._setInitValue();
		this._registerEvents();
	}

	_setInitValue() {
		this.elRecentWordLayer 		= this.elTarget.querySelector(".recent-word-wrap");
		this.elClearRecentWordBtn 	= this.elTarget.querySelector(".deleteWord");
		this.elCloseButtonRWL		= this.elRecentWordLayer.querySelector(".closeLayer");
		this.oStorage = new RecentWordPluginLocalStorageAddOn("searchQuery");
	}

	_setDefaultOption () {
		this._htDefaultOption = {
		}
	}

	_setDefaultFunction() {
		this._htDefaultFunction = {
			'fnInsertRecentSearchWord' : function(){}
		}
	}

	registerCallback(htFn) {
		this.htFn = {};
 		this._setDefaultFunction();
		super.execOption(htFn, this._htDefaultFunction, this.htFn);
	}

	_registerEvents() {
		this.elClearRecentWordBtn.addEventListener("touchend", (evt) => { this.handlerClearRecentWord(evt)});	
		this.elCloseButtonRWL.addEventListener("touchend", (evt) => { this.handlerCloseAllLayer(evt)});
	}

	handlerClearRecentWord(evt) {
		this.oStorage.removeKeywords();
		this.elRecentWordLayer.querySelector("ul").innerHTML = "";
		this.elClearRecentWordBtn.style.display = "none";
	}

	//TODO. duplicate
	handlerCloseAllLayer(evt) {
		this.elRecentWordLayer.style.display = "none";
	}

	showRecentSearchWord() {
		this.elRecentWordLayer.style.display = "block";
		let sData = this.oStorage.getKeywords();
		if(sData === null || sData === "") return;
		this.elClearRecentWordBtn.style.display = "block";
		let aData = JSON.parse(sData);
		this.htFn.fnInsertRecentSearchWord(aData);
	}

}

class  RecentWordPluginLocalStorageAddOn {
	
	constructor(sKey) {
		this.sKey = sKey;
	}

	saveKeyword(sKeyword) {
		if(this._validStorage()) return;

		let aLegacy = this.getKeywords();

		//to Array
		if(aLegacy === null) aLegacy = []; 
		else aLegacy = JSON.parse(aLegacy);

		//save data 
		let nIndex = aLegacy.indexOf(sKeyword);
		if( nIndex > -1) {
			if(nIndex === (aLegacy.length-1)) return;
			aLegacy.splice(nIndex, 1);
		} 

		aLegacy.push(sKeyword);
		localStorage.setItem(this.sKey, JSON.stringify(aLegacy));
	}

	getKeywords(){ 
		if(this._validStorage()) return;

		let sResult = localStorage.getItem(this.sKey);
		return sResult;
	}

	removeKeywords() {
		if(this._validStorage()) return;

		return localStorage.removeItem(this.sKey);
	}

	_validStorage() {
		if( typeof(Storage) === "undefined") return;
	}

}
class SmartSearch extends CommonComponent {
	
	constructor(elTarget, htOption) {
		super(htOption)
		this.elTarget = elTarget;
		this.init(htOption);
	}

	init(htOption) {
		this._setDefaultOption();
		//option variable declaration
		this.option = {};
		super.execOption(htOption, this._htDefaultOption, this.option);
		this._setInitValue();
		this._registerEvents();
	}

	_setInitValue() {
		const _cssSelector = {
			inputField 			: ".input-field",
			autoCompleteWrap 	: ".auto-complete-wrap",
			closeLayer 			: ".closeLayer",
			clearQueryBtn 		: ".clearQuery"
		} 

		this.elInputField 			= this.elTarget.querySelector(_cssSelector.inputField);
		this.elAutoCompleteLayer 	= this.elTarget.querySelector(_cssSelector.autoCompleteWrap);
		this.elCloseButton 			= this.elAutoCompleteLayer.querySelector(_cssSelector.closeLayer);
		this.elClearQueryBtn 		= this.elTarget.querySelector(_cssSelector.clearQueryBtn);
		this.htCachedData 			= {};

		//for plugin
		this.aPluginList			= ['RecentWordPlugin'];
		this.htPluginInstance 		= {};
	}

	_setDefaultOption () {
		this._htDefaultOption = {
			//'bCircular' : false,
		}
	}

	_setDefaultFunction() {
		this._htDefaultFunction = {
			'fnInsertAutoCompleteWord' : function(){},
		}
	}

	_registerEvents() {
		this.elInputField.addEventListener("focus" , 	(evt) => this.handlerInputFocus(evt));
		this.elInputField.addEventListener("keypress", 	(evt) => this.handlerInputKeyPress(evt));
		this.elInputField.addEventListener("keydown", 	(evt) => this.handlerInputKeydown(evt));
		this.elInputField.addEventListener("input", 	(evt) => this.handlerInputKeyInput(evt));
		this.elCloseButton.addEventListener("touchend", (evt) => this.handlerCloseAllLayer(evt));
		this.elClearQueryBtn.addEventListener("touchend", (evt) => this.handlerClearInputValue(evt));
	}

	registerCallback(htFn) {
		this.htFn = {};
 		this._setDefaultFunction();
		super.execOption(htFn, this._htDefaultFunction, this.htFn);
	}

	/* start EVENT-HANDLER */ 
	handlerInputFocus(evt) {
		CommonUtil.setCSS(this.elClearQueryBtn, "display", "inline-block");
		this.execAfterFocus(evt);
	}

	//입력필드에 들어가는 값의 어떠한 처리가 필요할때 여기서 처리한다.
	handlerInputKeyPress(evt) {}
	
	//특수키(keycode 8인 backspace등) 작업 조정이 필요한 경우 여기서 처리.
	handlerInputKeydown(evt) {}

	handlerInputKeyInput(evt) {
		let sInputData = this.elInputField.value;
		console.log("input evet fired : ", sInputData);
		if (typeof this.htCachedData[sInputData] === "undefined") this._makeAutoCompleteAjaxRequest(sInputData);
		else this.execAfterAutoCompleteAjax(sInputData, this.htCachedData[sInputData]);
	}

	handlerClearInputValue(evt) {
		this.elInputField.value = "";
		this.handlerCloseAllLayer();
	}
	
	handlerCloseAllLayer(evt) {
		CommonUtil.setCSS(this.elAutoCompleteLayer, "display", "none");
	}

	execAfterFocus(evt) {
		//execute RecentWordPlugin.
		let oRecentWordPlugin = this.htPluginInstance["RecentWordPlugin"];
		if(!oRecentWordPlugin) return;
		oRecentWordPlugin.showRecentSearchWord(this.htFn.fnInsertRecentSearchWord);

		//execute other plugin or other logic.
	}

	execAfterAutoCompleteAjax(sQuery, sResult) {
		//user customed function
		this.htFn.fnInsertAutoCompleteWord(sResult);

		//save history
		this.htCachedData[sQuery] = sResult;

		//save keyword to localstorage 
		//this.saveKeyword(sQuery);
	}

	_makeAutoCompleteAjaxRequest(sQuery) {
		let url = "../jsonMock/"+ sQuery +".json";
		let aHeaders = [["Content-Type", "application/json"]];

		this.sendSimpleAjax(url, this.execAfterAutoCompleteAjax.bind(this, sQuery), 
			JSON.stringify({
				sQuery : sQuery,
				nTime : Date.now() 
			}), 
		"get", aHeaders, sQuery);
	}

	//TODO. move to a CommmonComponent or AjaxUtils.
	sendSimpleAjax(url, fnCallback, sData, method, aHeaders, sQuery) {

		let xhr = new XMLHttpRequest();
		xhr.open(method, url);

		aHeaders.forEach( (v) => {
			xhr.setRequestHeader(v[0], v[1]);
		});

		xhr.addEventListener("load", function() {
			if (xhr.status === 200) {
				var sResult = JSON.parse(xhr.responseText);
				fnCallback.call(this, sResult);
			}
		}.bind(this));
		xhr.send(sData);
	}

	addOnPlugin(fnName) {
		return this._addOnPlugin(fnName, this.htPluginInstance, this.aPluginList, this.elTarget);
	}

}
